import React, { useState } from "react";
import { AiOutlineExclamationCircle } from "react-icons/ai";

import "./MessagePopup.css";

const MessagePopup = () => {
  const [messages, setMessages] = useState([]); // Az zenetek tmbje

  const showMessage = (
    msg,
    timeout,
    color = "#e83b51",
    textcolor = "white"
  ) => {
    // Szn hozzadsa, alaprtelmezett rtke piros
    const newMessage = {
      id: Date.now(), // Egyedi azonost a popupoknak
      text: msg,
      visible: true,
      color: color, // Szn trolsa
      textcolor: textcolor,
    };

    // Hozzadjuk az j zenetet
    setMessages((prevMessages) => [...prevMessages, newMessage]);

    // Az zenet eltnst kvet idzt
    setTimeout(() => {
      setMessages((prevMessages) =>
        prevMessages.map((message) =>
          message.id === newMessage.id
            ? { ...message, visible: false }
            : message
        )
      );
      // Az zenet eltvoltsa a kdbl 1 msodperccel az eltns utn (lehetv tve az animci futst)
      setTimeout(() => {
        setMessages((prevMessages) =>
          prevMessages.filter((message) => message.id !== newMessage.id)
        );
      }, 400); // A msodik setTimeout a popup eltvoltshoz szksges idt ad a kivonshoz
    }, timeout);
  };

  // Globlisan elrhet fggvny, amit a szl komponens hvhat
  window.showPopupMessage = showMessage;

  return (
    <div>
      {/* A popupok listja */}
      <div className="popup-container">
        {messages.map((message) => (
          <div
            key={message.id}
            className={`message-popup ${
              message.visible ? "popupVisible" : "popupHidden"
            }`}
            style={{ backgroundColor: message.color, color: message.textcolor }} // Szn belltsa
          >
            <div className="message-popup-icon">
              <AiOutlineExclamationCircle />
            </div>

            <div>
              <p className="popup-message">{message.text}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

// hasznlat: indow.showPopupMessage("Oho - Let's include at least 1 maybe :)", 3000, "#77DD77", "black" );

export default MessagePopup;
