import { useState, useCallback } from "react";

const useNoteDashboard = (mainTestBank, userNotesBank) => {
  const [isDetailVisible, setIsDetailVisible] = useState(false);
  const [activeCardId, setActiveCardId] = useState(null);
  const [isClosing, setIsClosing] = useState(false);
  const [searchTerm, setSearchTerm] = useState("");

  const getQuestionById = useCallback(
    (id) => {
      for (const topic of mainTestBank) {
        const question = topic.questions.find((q) => q.id === id);
        if (question) return question;
      }
      return null; // Ha nem tallhat krds, null-t adunk vissza
    },
    [mainTestBank]
  );

  const handleCardClick = useCallback((id) => {
    setIsDetailVisible(true);

    setTimeout(() => {
      setActiveCardId(id);
      setIsClosing(false);
    }, 200);
  }, []);

  const handleCloseDetails = () => {
    setIsClosing(true);
    setTimeout(() => {
      setIsDetailVisible(false);
      setActiveCardId(null);
      setIsClosing(false);
    }, 200);
  };

  const getFilteredNotes = useCallback(() => {
    return Object.entries(userNotesBank).filter(([id, note, subtopic]) => {
      const question = getQuestionById(id);
      if (!question) return false; // Ha nincs krds, kihagyjuk
      return (
        (note && note.toLowerCase().includes(searchTerm.toLowerCase())) || // Ellenrizzk, hogy a note nem undefined
        (question.id &&
          question.id.toLowerCase().includes(searchTerm.toLowerCase())) || // Ellenrizzk, hogy az id nem undefined
        (question.subtopic &&
          question.subtopic.toLowerCase().includes(searchTerm.toLowerCase())) // Ellenrizzk, hogy a topic nem undefined
      );
    });
  }, [userNotesBank, searchTerm, getQuestionById]);

  const activeQuestion = activeCardId ? getQuestionById(activeCardId) : null;
  const activeNote = activeCardId ? userNotesBank[activeCardId] : null;

  return {
    isDetailVisible,
    activeCardId,
    isClosing,
    searchTerm,
    setSearchTerm,
    handleCardClick,
    handleCloseDetails,
    getQuestionById,
    filteredNotes: getFilteredNotes(),
    activeQuestion,
    activeNote,
  };
};

export default useNoteDashboard;
